import React, { Component } from 'react';
import Country from './components/Country';
import Container from '@material-ui/core/Container';
import { Grid } from '@material-ui/core';
import Badge from '@material-ui/core/Badge';
import { withStyles } from '@material-ui/core/styles';
import './App.css';

const styles = {
  appHeading: {
    textAlign: 'center',
    margin: 0,
    backgroundColor: '#BBBBF1',
    position: 'fixed',
    width: '100%',
    paddingTop: '15px',
    paddingBottom: '4px',
    zIndex: '1000',
    opacity: '.95',
  },
  appHeadingBadge: {
    fontSize: '2.0em',
    fontFamily: 'Teko, sans-serif',
  },
  appHeadingText: {
    paddingRight: '8px'
  },
}

class App extends Component {
  state = {
    countries: [
      { id: 1, name: 'United States', gold: 2, silver: 2, bronze: 3 },
      { id: 2, name: 'China', gold: 3, silver: 1, bronze: 0 },
      { id: 3, name: 'Germany', gold: 0, silver: 2, bronze: 2 },
    ],
    medals: [
      { id: 1, name: 'gold', RGBColor: '#ffc107' },
      { id: 2, name: 'silver', RGBColor: '#c4cacc' },
      { id: 3, name: 'bronze', RGBColor: '#cd7f32' },
    ]
  }
  handleIncrement = (countryId, medalName) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    countries[idx][medalName] += 1;
    this.setState({ countries: countries });
  }
  handleDecrement = (countryId, medalName) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    countries[idx][medalName] -= 1;
    this.setState({ countries: countries });
  }
  getAllMedalsTotal() {
    let sum = 0;
    this.state.medals.forEach(medal => { sum += this.state.countries.reduce((a, b) => a + b[medal.name], 0); });
    return sum;
  }
  render() { 
    const {classes} = this.props;
    return (
      <React.Fragment>
        <div className={classes.appHeading}>
          <Badge 
            className={classes.appHeadingBadge}
            badgeContent={ this.getAllMedalsTotal() } 
            color='primary'>
            <div className={classes.appHeadingText}>Olympic Medals</div>
          </Badge>
        </div>
        <Container fixed={true} style={{paddingTop: '70px', paddingBottom: '20px'}}>
          <Grid spacing={2} justifyContent='center' container>
            { this.state.countries.map(country => 
              <Grid item key={ country.id }>
                <Country 
                  country={ country } 
                  medals={ this.state.medals }
                  onIncrement={ this.handleIncrement } 
                  onDecrement={ this.handleDecrement } />
              </Grid>
            )}
          </Grid>
        </Container>
      </React.Fragment>
    );
  }
}
 
export default withStyles(styles, { withTheme: true })(App);
