import React, { Component } from 'react';
import { Avatar, IconButton } from '@material-ui/core';
import AddBoxOutlinedIcon from '@material-ui/icons/AddBoxOutlined';
import IndeterminateCheckBoxOutlinedIcon from '@material-ui/icons/IndeterminateCheckBoxOutlined';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  countryMedal: {
    marginTop: '10px',
    display: 'flex',
    alignItems: 'center',
  },
  medalAvatar: {
    color: 'black',
  },
  medalName: {
    fontSize: '1.4em',
    paddingLeft: '10px',
    width: '130px',
    textTransform: 'capitalize',
  },
  iconButton: {
    padding: '2px',
  }
}

class Medal extends Component {
  state = {  }
  render() { 
    const { medal, country, onIncrement, onDecrement, classes } = this.props;
    return (
      <div className={classes.countryMedal}>
        <Avatar style={{ backgroundColor: medal.RGBColor }} className={classes.medalAvatar}>
          {country[medal.name]}
        </Avatar>
        <span className={classes.medalName}>
          {medal.name} Medals
        </span>
        <IconButton
          onClick={ () => onIncrement(country.id, medal.name) } 
          className={classes.iconButton} 
          color='primary' 
          aria-label='increment gold'>
          <AddBoxOutlinedIcon fontSize='large' />
        </IconButton>
        <IconButton
          disabled={country[medal.name] === 0}
          onClick={ () => onDecrement(country.id, medal.name) }
          className={classes.iconButton}
          color='secondary' 
          aria-label='decrement gold'>
          <IndeterminateCheckBoxOutlinedIcon fontSize='large' />
        </IconButton>
      </div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(Medal);
